









































.details{
    width: 80%;
    box-sizing: border-box;
    background: white;
    margin: auto;
    .title_wen{
      width: 100%;
      padding: 20px;
      box-sizing: border-box;
      background: white;
      border: #cccccc solid 1px;
      transition: all ease 1s;
      display: flex;
      align-items: center;
      justify-content: space-around;
      .img{
          width: 34%;
          img{
              width: 100%;
              height: 185px;
          }
      }
      p{
          font-size: 16px;
          margin-bottom: 5px;
      }
      .wen{
          width: 62%;
          .wenTitle{
              font-size: 18px;
              margin-bottom: 10px;
          }
          .span span{
              padding: 3px 5px 3px 0px;
              margin-right: 20px;
              font-size: 16px;
              i{
                  color: red;
              }
          }
      }
      
  }
  .title_wen:hover{
      box-shadow: 2px 0 20px #cccccc;
  }
  .info{
      padding: 20px;
       h2{
          text-align: center;
      }
      .content{
          p{
              text-indent: 2em;
          }
      }
  }
}  
